.menu {
    position: fixed;
    top: 2px;
    right: 15px;
    background: transparent;
    z-index: 100;
    transition: all 0.3s;
  }
  
  .menu ul {
    list-style: none;
  }
  
  .menu li {
    display: block;
    float: right;
    padding: 1rem;
    position: relative;
    text-decoration: none;
    transition-duration: 0.5s;
    cursor: pointer;
  }
  
  .menu-icon {
    font-size: 1.6rem;
    color: var(--color-orange);
  }
  
  .menu-icon-click, .menu-icon:hover {
    color: var(--color-red);
  }
    
  .menu ul li ul li a {
    width: 100%;
    height: 100%;
    color: var(--color-lightWhite);
    font-weight: bold;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;
  }
  
  .menu ul li ul li a i {
    color: var(--color-yellow);
  }
  
  .dropdown {
    background-color: var(--background-color);
    box-shadow: var(--color-black) 0px 3px 8px;
    border-radius: 15px;
    opacity: 0;
    visibility: hidden;
    min-width: 175px;
    position: absolute;
    margin-top: 1rem;
    right: 0;
    display: block;
  }
  .menu ul li ul li {
    width: 100%;
    height: 100%;
    text-align: center;
  }
  
  .menu ul li ul li:hover {
    border-radius: 15px;
    background-color: rgba(0, 0, 0, 0.2);
  }
  
  .menu ul li ul li:hover a i {
    color: var(--color-orange);
  }
  
  .dropdown.active {
    visibility: visible;
    opacity: 1;
    display: block;
  }